import { Swiper, SwiperSlide } from 'swiper/react';
import {Mousewheel } from 'swiper';
import 'swiper/css';
import 'swiper/css/mousewheel';
import { useState } from 'react';
import { useEffect } from 'react';

function MainTeam() {
    let raezmer = 3
    const [raz, setRaz] = useState(raezmer)
    useEffect(()=>{
        setRaz(raezmer = document.getElementById('swoper').offsetWidth/ 340)
    })
    return (
      <div id='team' className="main-team main-block-padding">
        <div className="conteiner">
            <div className="main-inner">
                <div className="main-team-content">
                    <div className="main-team-content-title"></div>
                    <Swiper
                    id='swoper'
                    modules={[Mousewheel]}
                    mousewheel
                    spaceBetween={5}
                    slidesPerView={raz}
                    >
                        <SwiperSlide>
                            <div className="card-client">
                                <div className="user-picture one">
                                    
                                </div>
                                <p className="name-client">  
                                    <span> 
                                    </span>
                                </p>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="card-client">
                                <div className="user-picture two">
                                    
                                </div>
                                <p className="name-client">  
                                    <span>     
                                    </span>
                                </p>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="card-client">
                                <div className="user-picture free">
                                    
                                </div>
                                <p className="name-client">  
                                    <span>   WEB  
                                    </span>
                                </p>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="card-client">
                                <div className="user-picture for">
                                
                                </div>
                                <p className="name-client">  
                                    <span> 
                                    </span>
                                </p>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="card-client">
                                <div className="user-picture five">
                                
                                </div>
                                <p className="name-client"> 
                                    <span>      
                                    </span>
                                </p>
                            </div>
                        </SwiperSlide>
                    </Swiper>
                </div>
            </div>
        </div>
      </div>
    );
  }
  
  export default MainTeam;